import {
  Button,
  Card,
  CardContent,
  Grid,
  makeStyles,
  Typography,
} from "@material-ui/core";
import WhatsAppIcon from "@material-ui/icons/WhatsApp";
import React from "react";

const useStyles = makeStyles({
  title: {
    fontSize: 24,
    margin: "auto 0 auto .6rem",
  },
});

export function Callforhelp() {
  const classes = useStyles();
  return (
    <Card variant="outlined" style={{ marginBottom: "1rem" }}>
      <CardContent style={{ paddingBottom: 16 }}>
        <Grid container>
          <Grid item xs={6} sm={8}>
            <Typography
              className={classes.title}
              color="textSecondary"
              gutterBottom
            >
              Need Help?
            </Typography>
          </Grid>
          <Grid item xs={6} sm={4}>
            <Button
              size="large"
              startIcon={<WhatsAppIcon />}
              fullWidth
              onClick={() => {
                window.open(
                  "https://wa.me/6282115030080?text=Hi%20Villa%20Akmali.%20Can%20you%20help%20me%20please?",
                  "_blank"
                );
              }}
            >
              Contact Us
            </Button>
          </Grid>
        </Grid>
      </CardContent>
    </Card>
  );
}
