import React, { useState } from "react";
import Ordermenu from "./orders/Ordermenu";
import Categorymenu from "./orders/Categorymenu";

export default function Orders() {
  const [triger, settriger] = useState(true);
  const refresh = () => {
    settriger(!triger);
  };
  return (
    <>
      <Categorymenu refresh={refresh} />
      <Ordermenu triger={triger} refresh={refresh} />
    </>
  );
}
