import React, { useEffect, useState } from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import AttachMoneyIcon from "@material-ui/icons/AttachMoney";
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from "@material-ui/core/styles";
import SearchIcon from "@material-ui/icons/Search";
import RefreshIcon from "@material-ui/icons/Refresh";
import axios from "axios";
import { Header, Url } from "../../../api/config";

const styles = (theme: Theme) =>
  createStyles({
    paper: {
      maxWidth: 936,
      margin: "auto",
      overflow: "hidden",
    },
    searchBar: {
      borderBottom: "1px solid rgba(0, 0, 0, 0.12)",
    },
    searchInput: {
      fontSize: theme.typography.fontSize,
    },
    block: {
      display: "block",
    },
    addUser: {
      marginRight: theme.spacing(1),
    },
    contentWrapper: {
      margin: "20px 16px",
    },
  });

export interface ICond {
  income: number;
  expense: number;
}

export interface LabelProps extends WithStyles<typeof styles> {}

function LabelCard(props: LabelProps) {
  const { classes } = props;
  const [cond, setcond] = useState<ICond>({
    income: 0,
    expense: 0,
  });
  useEffect(() => {
    axios
      .get(`${Url}/order/sumall`, { headers: Header })
      .then(function (response: any) {
        let val = response.data.data;
        console.log(val);
        setcond({
          income: val.input,
          expense: val.output,
        });
      });
  }, []);

  return (
    <Grid container className={classes.paper} spacing={2}>
      <Grid item xs={12} md={6}>
        <Paper className={classes.paper}>
          <div className={classes.contentWrapper}>
            <Typography align="center" variant="h6">
              Income
            </Typography>
            <Typography align="center" variant="h6" style={{ color: "green" }}>
              Rp {cond.income}
            </Typography>
          </div>
        </Paper>
      </Grid>
      <Grid item xs={12} md={6}>
        <Paper className={classes.paper}>
          <div className={classes.contentWrapper}>
            <Typography align="center" variant="h6">
              Expense
            </Typography>
            <Typography align="center" variant="h6" style={{ color: "red" }}>
              Rp {cond.expense}
            </Typography>
          </div>
        </Paper>
      </Grid>
    </Grid>
  );
}

export default withStyles(styles)(LabelCard);
