import {
  ButtonBase,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import React, { useState } from "react";

const image = [
  {
    url: "https://www.rukita.co/stories/wp-content/uploads/2020/02/byurger.jpg",
    title: "Burger",
    price: "30.000",
    width: "60%",
  },
  {
    url:
      "https://assets.pikiran-rakyat.com/crop/0x0:0x0/x/photo/2020/06/11/3650390209.jpg",
    title: "Pizza",
    price: "70.000",
    width: "40%",
  },
  {
    url:
      "https://www.nespresso.com/ncp/res/uploads/recipes/nespresso-recipes-Cappuccino-Creme-brulee.jpg",
    title: "Creme Brulle Latte",
    price: "24.000",
    width: "70%",
  },
  {
    url:
      "https://ds393qgzrxwzn.cloudfront.net/resize/m600x500/cat1/img/images/0/GBATXlxns6.jpg",
    title: "Americano",
    price: "22.000",
    width: "30%",
  },
];

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexWrap: "wrap",
      minWidth: 300,
      width: "100%",
    },
    image: {
      position: "relative",
      height: 350,
      [theme.breakpoints.down("xs")]: {
        width: "100% !important", // Overrides inline-style
        height: 220,
      },
      "&:hover, &$focusVisible": {
        zIndex: 1,
        "& $imageBackdrop": {
          opacity: 0.15,
        },
        "& $imageMarked": {
          opacity: 0,
        },
        "& $imageTitle": {
          border: "4px solid currentColor",
        },
      },
    },
    focusVisible: {},
    imageButton: {
      position: "absolute",
      left: 0,
      right: 0,
      top: 0,
      bottom: 0,
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      color: theme.palette.common.white,
    },
    imageSrc: {
      position: "absolute",
      left: 0,
      right: 0,
      top: 0,
      bottom: 0,
      backgroundSize: "cover",
      backgroundPosition: "center 40%",
    },
    imageBackdrop: {
      position: "absolute",
      left: 0,
      right: 0,
      top: 0,
      bottom: 0,
      backgroundColor: theme.palette.common.black,
      opacity: 0.4,
      transition: theme.transitions.create("opacity"),
    },
    imageTitle: {
      position: "relative",
      padding: `${theme.spacing(2)}px ${theme.spacing(4)}px ${
        theme.spacing(1) + 6
      }px`,
    },
    imageMarked: {
      height: 3,
      width: 18,
      backgroundColor: theme.palette.common.white,
      position: "absolute",
      bottom: -2,
      left: "calc(50% - 9px)",
      transition: theme.transitions.create("opacity"),
    },
  })
);
interface ImagelistProps {
  information: { url: string; title: string; price: string; width: string }[];
}
export function ImageList(props: ImagelistProps) {
  const classes = useStyles();
  const { information } = props;
  const [Route, setRoute] = useState(0);

  const handleChange = (route: number) => {
    setRoute(route);
  };

  return (
    <div className={classes.root}>
      {information.map((image, index) => (
        <ButtonBase
          focusRipple
          key={image.title}
          className={classes.image}
          focusVisibleClassName={classes.focusVisible}
          style={{
            width: image.width,
          }}
          onClick={() => {
            handleChange(index + 1);
          }}
        >
          <span
            className={classes.imageSrc}
            style={{
              backgroundImage: `url(${image.url})`,
            }}
          />
          <span className={classes.imageBackdrop} />
          <span className={classes.imageButton}>
            <Typography
              component="h5"
              variant="h5"
              color="inherit"
              className={classes.imageTitle}
            >
              {image.title} <br />
              Rp {image.price}
              <span className={classes.imageMarked} />
            </Typography>
          </span>
        </ButtonBase>
      ))}
    </div>
  );
}
