import {
  Breadcrumbs,
  Button,
  Container,
  Grid,
  Link,
  Typography,
} from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { Foodnbev } from "../page_guess/Foodnbev";
import { Imagebut } from "../reusable/Imagebut";

const Information = [
  {
    id: 0,
    name: "Our Service",
  },
  {
    id: 1,
    name: "Food & Baverage",
  },
  {
    id: 2,
    name: "Massage",
  },
  {
    id: 3,
    name: "Spa",
  },
  {
    id: 4,
    name: "Misc",
  },
];

export function Hero() {
  const [Trigger, setTrigger] = useState(false);
  const [Comp, setComp] = useState(0);

  useEffect(() => {
    let route = localStorage.getItem("route");
    if (route === null) {
      setComp(0);
    } else {
      setComp(Number(route));
    }
  }, [Trigger]);

  const refresh = () => {
    setTrigger(!Trigger);
  };

  return (
    <Container>
      <Grid container spacing={0}>
        <Grid item xs={12}>
          <div
            style={{
              textAlign: "center",
              padding: "1.6rem",
            }}
          >
            <Typography
              variant="h3"
              style={{ color: "#A5A58D", fontWeight: "normal" }}
            >
              {Information[Comp].name}
            </Typography>
          </div>
        </Grid>
        <Breadcrumbs aria-label="breadcrumb" style={{ marginBottom: "1rem" }}>
          <Link
            color="inherit"
            onClick={() => {
              localStorage.clear();
              refresh();
            }}
          >
            Villa Akmali
          </Link>
          {Comp !== 0 ? (
            <Typography color="textPrimary">
              {Information[Comp].name}
            </Typography>
          ) : null}
        </Breadcrumbs>
        <Grid item xs={12}>
          {Comp === 0 ? (
            <Imagebut refresh={refresh} />
          ) : Comp === 1 ? (
            <Foodnbev />
          ) : (
            <Button
              onClick={() => {
                localStorage.clear();
                refresh();
              }}
            >
              come back to home
            </Button>
          )}
        </Grid>
        <Grid item xs={12} style={{ marginTop: "1.9rem" }}>
          <Typography
            variant="subtitle1"
            style={{ marginBottom: "0px", textAlign: "center" }}
          >
            Copyright Â© {new Date().getFullYear()} by BubuStudio. All rights
            reserved
          </Typography>
        </Grid>
      </Grid>
    </Container>
  );
}
