import React, { useEffect, useState } from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from "@material-ui/core/styles";
import TitleIcon from "@material-ui/icons/Title";
import {
  MenuItem,
  Select,
  InputLabel,
  FormControl,
  Typography,
  FormControlLabel,
  Radio,
} from "@material-ui/core";
import axios from "axios";
import { Header, Url } from "../../../../api/config";

const styles = (theme: Theme) =>
  createStyles({
    paper: {
      maxWidth: 936,
      margin: "auto",
      overflow: "hidden",
    },
    searchBar: {
      borderBottom: "1px solid rgba(0, 0, 0, 0.12)",
    },
    searchInput: {
      fontSize: theme.typography.fontSize,
    },
    block: {
      display: "block",
    },
    addUser: {
      marginRight: theme.spacing(1),
    },
    OrdersWrapper: {
      margin: "20px 16px",
    },
    formControl: {
      width: "100%",
      marginTop: "1rem",
    },
  });

export interface OrdersProps extends WithStyles<typeof styles> {
  triger: boolean;
  refresh: () => void;
}

const starter = {
  keterangan: "",
  category_id: 0,
  user_id: Number(localStorage.getItem("id")),
  type: "",
  image: "",
  jumlah: "Rp ",
  judul: "",
};

const starter_category = {
  id: 0,
  name: "None",
};

export interface ISubmit {
  keterangan: string;
  category_id: number;
  user_id: number;
  type: string;
  image: string;
  jumlah: string | number;
  judul: string;
}

export interface ICategory {
  id: number;
  name: string;
}

function OrdersMenu(props: OrdersProps) {
  const { classes, triger, refresh } = props;
  const [val, setval] = useState<ISubmit>(starter);
  const [imagetitle, setimagetitle] = useState("");
  const [category, setcategory] = useState<ICategory[]>([starter_category]);
  useEffect(() => {
    setval({ ...val, user_id: Number(localStorage.getItem("id")) });
    axios
      .get(`${Url}/category/all`, { headers: Header })
      .then(function (response: any) {
        let x: ICategory[] = [];
        x.push(starter_category);
        x.push(...response.data.data);
        setcategory(x);
      })
      .catch(function (error: any) {
        refresh();
      });
  }, [triger]);

  const submit = () => {
    let kirim = val;
    kirim.jumlah = Number(String(kirim.jumlah).split(" ")[1]);
    // console.log(kirim)
    axios
      .post(`${Url}/order/create`, kirim, { headers: Header })
      .then(function (response: any) {
        localStorage.setItem("page", "dashboard");
        localStorage.setItem("subpages", "1");
        window.location.reload();
      })
      .catch(function (error: any) {
        alert("Error, please contact IT Support!");
      });
  };

  return (
    <Paper className={classes.paper} style={{ marginTop: "1.5rem" }}>
      <AppBar
        className={classes.searchBar}
        position="static"
        color="default"
        elevation={0}
      >
        <Toolbar>
          <Grid container spacing={2} alignItems="center">
            <Grid item>
              <TitleIcon className={classes.block} color="inherit" />
            </Grid>
            <Grid item xs>
              <TextField
                fullWidth
                placeholder="Order Title"
                value={val.judul}
                onChange={(e) => {
                  setval({ ...val, judul: e.target.value });
                }}
                InputProps={{
                  disableUnderline: true,
                  className: classes.searchInput,
                }}
              />
            </Grid>
            <Grid item>
              <Button
                variant="contained"
                color="primary"
                onClick={submit}
                disabled={
                  val.category_id === 0 ||
                  /([0-9]|[a-z])/.test(val.image) === false ||
                  /([0-9]|[a-z])/.test(val.judul) === false ||
                  val.jumlah === "Rp " ||
                  /([0-9]|[a-z])/.test(val.keterangan) === false ||
                  val.type === ""
                }
                className={classes.addUser}
              >
                Add Orders
              </Button>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      <div className={classes.OrdersWrapper}>
        <form noValidate autoComplete="off">
          <TextField
            id="outlined-basic"
            label="Orders Description"
            variant="outlined"
            multiline
            value={val.keterangan}
            onChange={(e) => {
              setval({ ...val, keterangan: e.target.value });
            }}
            rows={4}
            fullWidth
          />
          <FormControl
            variant="outlined"
            className={classes.formControl}
            fullWidth
          >
            <InputLabel id="Category">Category</InputLabel>
            <Select
              variant="outlined"
              fullWidth
              label="Category"
              labelId="Category"
              id="Category"
              value={val.category_id}
              onChange={(e) => {
                setval({ ...val, category_id: Number(e.target.value) });
              }}
            >
              {category.map((cat) =>
                cat.id === 0 ? (
                  <MenuItem value={cat.id}>
                    <em>{cat.name}</em>
                  </MenuItem>
                ) : (
                  <MenuItem value={cat.id}>{cat.name}</MenuItem>
                )
              )}
            </Select>
          </FormControl>
          <TextField
            id="outlined-basic"
            label="Amount"
            variant="outlined"
            value={val.jumlah}
            onChange={(e) => {
              if (e.target.value.length < 4) {
                setval({ ...val, jumlah: "Rp " });
              } else {
                if (/[0-9]+$/g.test(e.target.value))
                  setval({ ...val, jumlah: e.target.value });
              }
            }}
            fullWidth
            style={{ margin: "1rem 0" }}
          />
          <Grid container style={{ marginBottom: "16px" }}>
            <FormControlLabel
              value="end"
              control={
                <Radio
                  color="primary"
                  checked={val.type === "input"}
                  onClick={() => {
                    setval({ ...val, type: "input" });
                  }}
                />
              }
              label="Income"
            />
            <FormControlLabel
              value="end"
              checked={val.type === "output"}
              onClick={() => {
                setval({ ...val, type: "output" });
              }}
              control={<Radio color="primary" />}
              label="Expense"
            />
          </Grid>
          <Grid container>
            <Button variant="contained" color="primary" component="label">
              Upload File
              <input
                type="file"
                accept="image/*"
                onChange={(e) => {
                  if (e.target.files == null) {
                  } else {
                    var file = e.target.files[0];
                    var reader = new FileReader();
                    reader.readAsDataURL(file);
                    reader.onload = (e) => {
                      setval({ ...val, image: String(reader.result) });
                    };
                    setimagetitle(file.name);
                  }
                }}
                capture
                hidden
              />
            </Button>
            <Typography
              color="textSecondary"
              align="center"
              style={{
                marginTop: "auto",
                marginBottom: "auto",
                marginLeft: ".4rem",
              }}
            >
              {imagetitle}
            </Typography>
          </Grid>
        </form>
      </div>
    </Paper>
  );
}

export default withStyles(styles)(OrdersMenu);
