import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from "@material-ui/core/styles";
import SearchIcon from "@material-ui/icons/Search";
import RefreshIcon from "@material-ui/icons/Refresh";
import CloseIcon from "@material-ui/icons/Close";
import "date-fns";
import DateFnsUtils from "@date-io/date-fns";
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from "@material-ui/pickers";
import {
  CardMedia,
  Checkbox,
  Divider,
  FormControlLabel,
  Slider,
} from "@material-ui/core";
import { DataGrid, ColDef, ValueGetterParams } from "@material-ui/data-grid";
import Pagination from "@material-ui/lab/Pagination";
import axios from "axios";
import { Header, Url } from "../../../api/config";
import FileSaver from "file-saver";

const styles = (theme: Theme) =>
  createStyles({
    paper: {
      maxWidth: 936,
      margin: "auto",
      overflow: "hidden",
    },
    searchBar: {
      borderBottom: "1px solid rgba(0, 0, 0, 0.12)",
    },
    searchInput: {
      fontSize: theme.typography.fontSize,
    },
    block: {
      display: "block",
    },
    addUser: {
      marginRight: theme.spacing(1),
    },
    contentWrapper: {
      margin: "16px 16px",
    },
  });

const columns: ColDef[] = [
  { field: "id", headerName: "ID", width: 80 },
  { field: "judul", headerName: "Title", width: 200 },
  { field: "category", headerName: "Category", width: 200 },
  { field: "user", headerName: "Creator", width: 200 },
  { field: "jumlah", headerName: "Jumlah", width: 180 },
];

const defaultcol = [
  {
    id: 1,
    judul: "",
    category: "",
    user: "",
    jumlah: 2000,
  },
];

function valuetext(value: number) {
  return `${value}Â°C`;
}

function dataURItoBlob(dataURI: any) {
  // convert base64 to raw binary data held in a string
  // doesn't handle URLEncoded DataURIs - see SO answer #6850276 for code that does this
  var byteString = atob(dataURI.split(",")[1]);

  // separate out the mime component
  var mimeString = dataURI.split(",")[0].split(":")[1].split(";")[0];

  // write the bytes of the string to an ArrayBuffer
  var ab = new ArrayBuffer(byteString.length);

  // create a view into the buffer
  var ia = new Uint8Array(ab);

  // set the bytes of the buffer to the correct values
  for (var i = 0; i < byteString.length; i++) {
    ia[i] = byteString.charCodeAt(i);
  }

  // write the ArrayBuffer to a blob, and you're done
  var blob = new Blob([ab], { type: mimeString });
  return blob;
}

export interface ContentProps extends WithStyles<typeof styles> {}

function Content(props: ContentProps) {
  const [selectedDate, setSelectedDate] = React.useState<Date | null>(
    new Date()
  );
  const [filter, sefilter] = React.useState(false);

  const handleDateChange = (date: Date | null) => {
    console.log({ date });
    setSelectedDate(date);
  };

  const [querydata, setquerydata] = React.useState(0);

  const { classes } = props;
  const [data, setdata] = React.useState(defaultcol);
  const [dataone, setdataone] = React.useState({
    id: 0,
    judul: "",
    keterangan: "",
    category: "",
    user: "",
    type: "",
    image: "",
    jumlah: 0,
    created_at: new Date(),
  });

  const [limit, setlimit] = React.useState(0);

  const [search, setsearch] = React.useState({
    page: 1,
    limit: 20,
  });

  React.useEffect(() => {
    axios
      .get(`${Url}/order/all?page=${search.page}&limit=${search.limit}`, {
        headers: Header,
      })
      .then(function (response: any) {
        let val = response.data.data;
        let lim = response.data.limit;
        setdata(val);
        setlimit(lim);
        console.log(val, lim);
      });
  }, []);

  React.useEffect(() => {
    if (search.page === 0) {
    } else {
      axios
        .get(`${Url}/order/all?page=${search.page}&limit=${search.limit}`, {
          headers: Header,
        })
        .then(function (response: any) {
          let val = response.data.data;
          let lim = response.data.limit;
          setdata(val);
          setlimit(lim);
          console.log(val, lim);
        });
    }
  }, [search.page, search.limit]);

  React.useEffect(() => {
    if (querydata === 0) {
    } else {
      axios
        .get(`${Url}/order/data/${querydata}`, {
          headers: Header,
        })
        .then(function (response: any) {
          setdataone(response.data.data);
        });
    }
  }, [querydata]);

  return (
    <>
      {querydata !== 0 ? (
        <Paper className={classes.paper} style={{ marginBottom: "1rem" }}>
          <AppBar
            className={classes.searchBar}
            position="static"
            color="default"
            elevation={0}
          >
            <Toolbar>
              <Grid container spacing={2} alignItems="center">
                <Grid item xs>
                  <TextField
                    fullWidth
                    disabled
                    value={dataone.created_at}
                    InputProps={{
                      disableUnderline: true,
                      className: classes.searchInput,
                    }}
                  />
                </Grid>
                <Grid item>
                  <Tooltip title="Reload">
                    <IconButton>
                      <CloseIcon
                        className={classes.block}
                        color="inherit"
                        onClick={() => setquerydata(0)}
                      />
                    </IconButton>
                  </Tooltip>
                </Grid>
              </Grid>
            </Toolbar>
          </AppBar>
          <div className={classes.contentWrapper}>
            <Grid container>
              <Grid item xs={12} md={6}>
                <Paper style={{ margin: "1rem" }}>
                  <img
                    src={`${dataone.image}`}
                    alt={dataone.judul}
                    style={{ width: "100%", height: "auto" }}
                  />
                </Paper>
              </Grid>
              <Grid item xs={12} md={6}>
                <Paper
                  style={{ margin: "1rem", padding: "1.5rem" }}
                  elevation={0}
                >
                  <Typography
                    align="left"
                    variant="h5"
                    style={{ marginBottom: ".6rem" }}
                  >
                    {dataone.judul}
                  </Typography>
                  <Typography
                    align="left"
                    variant="subtitle1"
                    style={{ marginBottom: ".6rem" }}
                  >
                    {dataone.keterangan}
                  </Typography>
                  <Typography
                    align="left"
                    variant="h6"
                    style={{
                      marginBottom: ".6rem",
                      color:
                        dataone.type === "Income"
                          ? "green"
                          : dataone.type === "Expense"
                          ? "red"
                          : "black",
                    }}
                  >
                    Rp {dataone.jumlah}
                  </Typography>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={() => {
                      let x = dataURItoBlob(dataone.image);
                      FileSaver.saveAs(x, String(dataone.created_at));
                    }}
                  >
                    Download Receipt
                  </Button>
                </Paper>
              </Grid>
            </Grid>
          </div>
        </Paper>
      ) : null}
      <Paper className={classes.paper}>
        <AppBar
          className={classes.searchBar}
          position="static"
          color="default"
          elevation={0}
        >
          <Toolbar>
            <Grid container spacing={2} alignItems="center">
              <Grid item>
                <SearchIcon className={classes.block} color="inherit" />
              </Grid>
              <Grid item xs>
                <TextField
                  fullWidth
                  disabled
                  InputProps={{
                    disableUnderline: true,
                    className: classes.searchInput,
                  }}
                />
              </Grid>
              <Grid item>
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => {
                    sefilter(!filter);
                  }}
                  className={classes.addUser}
                >
                  Filter Orders
                </Button>
                <Tooltip title="Reload">
                  <IconButton>
                    <RefreshIcon className={classes.block} color="inherit" />
                  </IconButton>
                </Tooltip>
              </Grid>
            </Grid>
          </Toolbar>
        </AppBar>
        {filter ? (
          <div className={classes.contentWrapper}>
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
              <Paper>
                <Grid container justify="space-around">
                  <KeyboardDatePicker
                    margin="normal"
                    id="date-picker-dialog"
                    label="From"
                    format="dd/MM/yyyy"
                    value={selectedDate}
                    onChange={handleDateChange}
                    KeyboardButtonProps={{
                      "aria-label": "change date",
                    }}
                  />
                  <KeyboardDatePicker
                    margin="normal"
                    id="date-picker-dialog"
                    label="To"
                    format="dd/MM/yyyy"
                    value={selectedDate}
                    onChange={handleDateChange}
                    KeyboardButtonProps={{
                      "aria-label": "change date",
                    }}
                  />
                </Grid>
                <Grid container justify="space-around">
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={false}
                        onChange={() => {}}
                        name="checkedB"
                        color="primary"
                      />
                    }
                    label="Income"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={false}
                        onChange={() => {}}
                        name="checkedB"
                        color="primary"
                      />
                    }
                    label="Expense"
                  />
                  <Button
                    variant="contained"
                    color="primary"
                    style={{
                      marginBottom: "1rem",
                      marginTop: "1rem",
                    }}
                    className={classes.addUser}
                  >
                    Submit
                  </Button>
                </Grid>
              </Paper>
            </MuiPickersUtilsProvider>
          </div>
        ) : null}
        <div className={classes.contentWrapper}>
          <Grid container>
            <Grid item xs={6}>
              <Typography id="discrete-slider" gutterBottom>
                Limit data per page
              </Typography>
              <Slider
                defaultValue={30}
                getAriaValueText={valuetext}
                aria-labelledby="discrete-slider"
                valueLabelDisplay="auto"
                step={20}
                marks
                min={20}
                max={200}
                value={search.limit}
                onChange={(e, val) => {
                  if (typeof val === "number") {
                    setsearch({
                      ...search,
                      limit: val,
                    });
                  }
                }}
              />
            </Grid>
            <Grid
              item
              xs={6}
              style={{
                display: "flex",
                justifyContent: "end",
                alignItems: "end",
              }}
            >
              <Paper
                elevation={0}
                style={{
                  display: "flex",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Pagination
                  count={limit}
                  defaultPage={1}
                  onChange={(e, p) => {
                    // window.location.replace(
                    //   `search?page=${p}&limit=${search.limit}`
                    // );
                    setsearch({
                      ...search,
                      page: p,
                    });
                  }}
                  color="primary"
                  style={{ marginLeft: "auto", marginBottom: "1rem" }}
                />
              </Paper>
            </Grid>
          </Grid>
          <div style={{ height: 600, width: "100%" }}>
            <DataGrid
              rows={data}
              columns={columns}
              pageSize={20}
              onRowClick={(e: any) => {
                setquerydata(Number(e.row.id));
              }}
            />
          </div>
        </div>
      </Paper>
    </>
  );
}

export default withStyles(styles)(Content);
