import React, { useState } from "react";
import clsx from "clsx";
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from "@material-ui/core/styles";
import Divider from "@material-ui/core/Divider";
import Drawer, { DrawerProps } from "@material-ui/core/Drawer";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import HomeIcon from "@material-ui/icons/Home";
import PeopleIcon from "@material-ui/icons/People";
import DnsRoundedIcon from "@material-ui/icons/DnsRounded";
import PermMediaOutlinedIcon from "@material-ui/icons/PhotoSizeSelectActual";
import TimerIcon from "@material-ui/icons/Timer";
import SettingsIcon from "@material-ui/icons/Settings";
import { Omit } from "@material-ui/types";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";

const categories = [
  {
    id: "Actions",
    children: [
      { id: "Orders", icon: <PeopleIcon />, active: false, pages: "orders" },
      // { id: "Costumers", icon: <DnsRoundedIcon /> },
      // { id: "Reports", icon: <PermMediaOutlinedIcon /> },
    ],
  },
  // {
  //   id: "Reports",
  //   children: [
  //     { id: "19/01/2021", icon: <SettingsIcon /> },
  //     { id: "18/01/2021", icon: <TimerIcon /> },
  //   ],
  // },
  {
    id: "Users",
    children: [
      { id: "Logout", icon: <ExitToAppIcon />, active: false, pages: "logout" },
    ],
  },
];

const styles = (theme: Theme) =>
  createStyles({
    categoryHeader: {
      paddingTop: theme.spacing(2),
      paddingBottom: theme.spacing(2),
    },
    categoryHeaderPrimary: {
      color: theme.palette.common.white,
    },
    item: {
      paddingTop: 1,
      paddingBottom: 1,
      color: "rgba(255, 255, 255, 0.7)",
      "&:hover,&:focus": {
        backgroundColor: "rgba(255, 255, 255, 0.08)",
      },
    },
    itemCategory: {
      backgroundColor: "#232f3e",
      boxShadow: "0 -1px 0 #404854 inset",
      paddingTop: theme.spacing(2),
      paddingBottom: theme.spacing(2),
    },
    firebase: {
      fontSize: 24,
      color: theme.palette.common.white,
    },
    itemActiveItem: {
      color: "#4fc3f7",
    },
    itemPrimary: {
      fontSize: "inherit",
    },
    itemIcon: {
      minWidth: "auto",
      marginRight: theme.spacing(2),
    },
    divider: {
      marginTop: theme.spacing(2),
    },
  });

export interface NavigatorProps
  extends Omit<DrawerProps, "classes">,
    WithStyles<typeof styles> {
  PaperProps: any;
  trigerer: any;
  refresh: () => void;
}

function Navigator(props: NavigatorProps) {
  const { trigerer, refresh, classes, ...rest } = props;
  const [Active, setActive] = useState(localStorage.getItem("page"));

  return (
    <Drawer variant="permanent" {...rest}>
      <List disablePadding>
        <ListItem
          className={clsx(classes.firebase, classes.item, classes.itemCategory)}
        >
          Villa Akmali
        </ListItem>
        <React.Fragment key="dashboard">
          <ListItem
            className={clsx(
              classes.item,
              classes.itemCategory,
              "dashboard" === Active && classes.itemActiveItem
            )}
            onClick={() => {
              setActive("dashboard");
              localStorage.setItem("page", "dashboard");
              refresh();
            }}
          >
            <ListItemIcon className={classes.itemIcon}>
              <HomeIcon />
            </ListItemIcon>
            <ListItemText
              classes={{
                primary: classes.itemPrimary,
              }}
            >
              Home
            </ListItemText>
          </ListItem>
        </React.Fragment>
        {categories.map(({ id, children }) => (
          <React.Fragment key={id}>
            <ListItem className={classes.categoryHeader}>
              <ListItemText
                classes={{
                  primary: classes.categoryHeaderPrimary,
                }}
              >
                {id}
              </ListItemText>
            </ListItem>
            {children.map(({ id: childId, icon, active, pages }) => (
              <ListItem
                key={childId}
                button
                onClick={() => {
                  if (pages) {
                    if (pages === "logout") {
                      localStorage.clear();
                      window.location.reload(false);
                    } else {
                      setActive(pages);
                      localStorage.setItem("page", pages.toString());
                      refresh();
                    }
                  }
                }}
                className={clsx(
                  classes.item,
                  pages === Active && classes.itemActiveItem
                )}
              >
                <ListItemIcon className={classes.itemIcon}>{icon}</ListItemIcon>
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {childId}
                </ListItemText>
              </ListItem>
            ))}
            <Divider className={classes.divider} />
          </React.Fragment>
        ))}
      </List>
    </Drawer>
  );
}

export default withStyles(styles)(Navigator);
