import React, { useState, useEffect } from "react";
import AppBar from "@material-ui/core/AppBar";
import Paper from "@material-ui/core/Paper";
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from "@material-ui/core/styles";
import {
  AreaChart,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Area,
  ResponsiveContainer,
} from "recharts";
import { Box, Grid, Tab, Tabs, Typography } from "@material-ui/core";
import { DataMonth, DataWeek, DataYear } from "../../../api/exam";
import axios from "axios";
import { Url, Header } from "../../../api/config";

interface TabPanelProps extends WithStyles<typeof styles> {
  children?: React.ReactNode;
  index: any;
  value: any;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;
  const { classes } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={1}>
          <div className={classes.contentWrapper}>
            <Grid container spacing={0}>
              <Grid item xs={12}>
                <div className={classes.graph}>{children}</div>
              </Grid>
            </Grid>
          </div>
        </Box>
      )}
    </div>
  );
}

const styles = (theme: Theme) =>
  createStyles({
    paper: {
      flexGrow: 1,
      maxWidth: 936,
      margin: "auto",
      overflow: "hidden",
    },
    searchBar: {
      borderBottom: "1px solid rgba(0, 0, 0, 0.12)",
    },
    searchInput: {
      fontSize: theme.typography.fontSize,
    },
    block: {
      display: "block",
    },
    addUser: {
      marginRight: theme.spacing(1),
    },
    contentWrapper: {
      margin: "40px 16px",
    },
    graph: {
      width: "auto",
      height: "20vh",
      [theme.breakpoints.down("md")]: {
        height: "25vh",
      },
    },
  });

function a11yProps(index: any) {
  return {
    id: `scrollable-auto-tab-${index}`,
    "aria-controls": `scrollable-auto-tabpanel-${index}`,
  };
}

export interface ContentProps extends WithStyles<typeof styles> {}

function Content(props: ContentProps) {
  const { classes } = props;
  const [value, setValue] = useState(0);
  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
  };

  const [week, setweek] = useState(DataWeek);
  const [month, setmonth] = useState(DataMonth);
  const [year, setyear] = useState(DataYear);

  const [data, setdata] = useState({
    week: DataWeek,
    month: DataMonth,
    year: DataYear,
  });
  useEffect(() => {
    axios
      .get(`${Url}/order/graphweek`, { headers: Header })
      .then(function (response: any) {
        let val = response.data.data;
        setweek(val);
      });
    axios
      .get(`${Url}/order/graphmonth`, { headers: Header })
      .then(function (response: any) {
        let val = response.data.data;
        setmonth(val);
      });
    axios
      .get(`${Url}/order/graphyear`, { headers: Header })
      .then(function (response: any) {
        let val = response.data.data;
        setyear(val);
      });
  }, []);

  return (
    <Paper className={classes.paper}>
      <AppBar position="static" color="default" elevation={0}>
        <Tabs
          value={value}
          onChange={handleChange}
          indicatorColor="primary"
          textColor="primary"
          variant="scrollable"
          scrollButtons="auto"
        >
          <Tab label="Weekly" {...a11yProps(0)} />
          <Tab label="Monthly" {...a11yProps(1)} />
          <Tab label="Yearly" {...a11yProps(2)} />
        </Tabs>
      </AppBar>
      <TabPanel value={value} index={0} classes={classes}>
        <ResponsiveContainer>
          <AreaChart
            width={600}
            height={250}
            data={week}
            margin={{ top: 10, right: 30, left: 0, bottom: 0 }}
          >
            <defs>
              <linearGradient id="colorIncome" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#8884d8" stopOpacity={0.8} />
                <stop offset="95%" stopColor="#8884d8" stopOpacity={0} />
              </linearGradient>
              <linearGradient id="Expense" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#82ca9d" stopOpacity={0.8} />
                <stop offset="95%" stopColor="#82ca9d" stopOpacity={0} />
              </linearGradient>
            </defs>
            <XAxis dataKey="name" />
            <YAxis />
            {/* <CartesianGrid strokeDasharray="3 3" /> */}
            <Tooltip />
            <Area
              type="monotone"
              dataKey="Income"
              stroke="#8884d8"
              fillOpacity={1}
              fill="url(#colorIncome)"
            />
            <Area
              type="monotone"
              dataKey="Expense"
              stroke="#82ca9d"
              fillOpacity={1}
              fill="url(#Expense)"
            />
          </AreaChart>
        </ResponsiveContainer>
      </TabPanel>
      <TabPanel value={value} index={1} classes={classes}>
        <ResponsiveContainer>
          <AreaChart
            width={600}
            height={250}
            data={month}
            margin={{ top: 10, right: 30, left: 0, bottom: 0 }}
          >
            <defs>
              <linearGradient id="colorIncome" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#8884d8" stopOpacity={0.8} />
                <stop offset="95%" stopColor="#8884d8" stopOpacity={0} />
              </linearGradient>
              <linearGradient id="Expense" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#82ca9d" stopOpacity={0.8} />
                <stop offset="95%" stopColor="#82ca9d" stopOpacity={0} />
              </linearGradient>
            </defs>
            <XAxis dataKey="name" />
            <YAxis />
            {/* <CartesianGrid strokeDasharray="3 3" /> */}
            <Tooltip />
            <Area
              type="monotone"
              dataKey="Income"
              stroke="#8884d8"
              fillOpacity={1}
              fill="url(#colorIncome)"
            />
            <Area
              type="monotone"
              dataKey="Expense"
              stroke="#82ca9d"
              fillOpacity={1}
              fill="url(#Expense)"
            />
          </AreaChart>
        </ResponsiveContainer>
      </TabPanel>
      <TabPanel value={value} index={2} classes={classes}>
        <ResponsiveContainer>
          <AreaChart
            width={600}
            height={250}
            data={year}
            margin={{ top: 10, right: 30, left: 0, bottom: 0 }}
          >
            <defs>
              <linearGradient id="colorIncome" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#8884d8" stopOpacity={0.8} />
                <stop offset="95%" stopColor="#8884d8" stopOpacity={0} />
              </linearGradient>
              <linearGradient id="Expense" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#82ca9d" stopOpacity={0.8} />
                <stop offset="95%" stopColor="#82ca9d" stopOpacity={0} />
              </linearGradient>
            </defs>
            <XAxis dataKey="name" />
            <YAxis />
            {/* <CartesianGrid strokeDasharray="3 3" /> */}
            <Tooltip />
            <Area
              type="monotone"
              dataKey="Income"
              stroke="#8884d8"
              fillOpacity={1}
              fill="url(#colorIncome)"
            />
            <Area
              type="monotone"
              dataKey="Expense"
              stroke="#82ca9d"
              fillOpacity={1}
              fill="url(#Expense)"
            />
          </AreaChart>
        </ResponsiveContainer>
      </TabPanel>
    </Paper>
  );
}

export default withStyles(styles)(Content);
