import {
  ButtonBase,
  createStyles,
  makeStyles,
  Theme,
  Typography,
} from "@material-ui/core";
import React, { useState } from "react";

const image = [
  {
    url: "/img/content_villa/20210115_213111.jpg",
    title: "Food & Beverage",
    width: "60%",
  },
  {
    url: "/img/content_villa/20210115_210741.jpg",
    title: "Massage & Spa",
    width: "40%",
  },
  {
    url:
      "https://q7m2m8q2.ssl.hwcdn.net/content/1024x680/destinazioni/stati/indonesia/citta/denpasar/viaggi/1a785472-ee64-44a7-bcb3-0a47eec09f3a/images/indonesia-bali-tirtagangga.jpg",
    title: "Bali Tour",
    width: "70%",
  },
  {
    url:
      "https://media1.s-nbcnews.com/i/newscms/2019_19/2847166/190506-stock-better-hotel-ew-937a_894bef33cdd0340ad32680c48a99214c.jpg",
    title: "Misc",
    width: "30%",
  },
];

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: "flex",
      flexWrap: "wrap",
      minWidth: 300,
      width: "100%",
    },
    image: {
      position: "relative",
      height: 350,
      [theme.breakpoints.down("xs")]: {
        width: "100% !important", // Overrides inline-style
        height: 220,
      },
      "&:hover, &$focusVisible": {
        zIndex: 1,
        "& $imageBackdrop": {
          opacity: 0.15,
        },
        "& $imageMarked": {
          opacity: 0,
        },
        "& $imageTitle": {
          border: "4px solid currentColor",
        },
      },
    },
    focusVisible: {},
    imageButton: {
      position: "absolute",
      left: 0,
      right: 0,
      top: 0,
      bottom: 0,
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      color: theme.palette.common.white,
    },
    imageSrc: {
      position: "absolute",
      left: 0,
      right: 0,
      top: 0,
      bottom: 0,
      backgroundSize: "cover",
      backgroundPosition: "center 40%",
    },
    imageBackdrop: {
      position: "absolute",
      left: 0,
      right: 0,
      top: 0,
      bottom: 0,
      backgroundColor: theme.palette.common.black,
      opacity: 0.4,
      transition: theme.transitions.create("opacity"),
    },
    imageTitle: {
      position: "relative",
      padding: `${theme.spacing(2)}px ${theme.spacing(4)}px ${
        theme.spacing(1) + 6
      }px`,
    },
    imageMarked: {
      height: 3,
      width: 18,
      backgroundColor: theme.palette.common.white,
      position: "absolute",
      bottom: -2,
      left: "calc(50% - 9px)",
      transition: theme.transitions.create("opacity"),
    },
  })
);

interface ImagebutProps {
  refresh: () => void;
}

export function Imagebut(props: ImagebutProps) {
  const { refresh } = props;
  const classes = useStyles();
  const [Route, setRoute] = useState(0);

  const handleChange = (route: number) => {
    setRoute(route);
    localStorage.setItem("route", route.toString());
    refresh();
  };

  return (
    <div className={classes.root}>
      {image.map((image, index) => (
        <ButtonBase
          focusRipple
          key={image.title}
          className={classes.image}
          focusVisibleClassName={classes.focusVisible}
          style={{
            width: image.width,
          }}
          onClick={() => {
            handleChange(index + 1);
          }}
        >
          <span
            className={classes.imageSrc}
            style={{
              backgroundImage: `url(${image.url})`,
            }}
          />
          <span className={classes.imageBackdrop} />
          <span className={classes.imageButton}>
            <Typography
              component="h5"
              variant="h5"
              color="inherit"
              className={classes.imageTitle}
            >
              {image.title}
              <span className={classes.imageMarked} />
            </Typography>
          </span>
        </ButtonBase>
      ))}
    </div>
  );
}
