import { useEffect, useState } from "react";
import "./App.css";
import Auth from "./page/auth/auth";
import Paperbase from "./page/dashboard/main";
import { HomeGuest } from "./page/guess/Home";

function App() {
  const [Level, setLevel] = useState("");
  const [Guest, setGuest] = useState(true);
  const [Triger, setTriger] = useState(false);

  useEffect(() => {
    if (window.location.href.split("/")[3].includes("staff")) {
      setGuest(false);
      let lv: any = "";
      lv = localStorage.getItem("level");
      setLevel(lv);
    } else {
    }
  }, [Triger]);

  const Refresh = () => {
    setTriger(!Triger);
  };

  return (
    <>
      {Guest ? (
        <>
          <HomeGuest />
        </>
      ) : (
        <>
          {Level === "1" ? (
            <Paperbase />
          ) : (
            <Auth Refresh={Refresh} Triger={Triger} />
          )}
        </>
      )}
    </>
  );
}

export default App;
