import React, { useEffect, useState } from "react";
import AppBar from "@material-ui/core/AppBar";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import HelpIcon from "@material-ui/icons/Help";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import Link from "@material-ui/core/Link";
import MenuIcon from "@material-ui/icons/Menu";
import NotificationsIcon from "@material-ui/icons/Notifications";
import Tab from "@material-ui/core/Tab";
import Tabs from "@material-ui/core/Tabs";
import Toolbar from "@material-ui/core/Toolbar";
import Tooltip from "@material-ui/core/Tooltip";
import Typography from "@material-ui/core/Typography";
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from "@material-ui/core/styles";

const lightColor = "rgba(255, 255, 255, 0.7)";

const styles = (theme: Theme) =>
  createStyles({
    secondaryBar: {
      zIndex: 0,
    },
    menuButton: {
      marginLeft: -theme.spacing(1),
    },
    iconButtonAvatar: {
      padding: 4,
    },
    link: {
      textDecoration: "none",
      color: lightColor,
      "&:hover": {
        color: theme.palette.common.white,
      },
    },
    button: {
      borderColor: lightColor,
    },
  });

interface HeaderProps extends WithStyles<typeof styles> {
  onDrawerToggle: any;
  trigerer: any;
  refresh: () => void;
}

function Header(props: HeaderProps) {
  const { classes, onDrawerToggle, trigerer, refresh } = props;
  const [pages, setpages] = useState(localStorage.getItem("page"));
  const [subpages, setsubpages] = useState(
    Number(localStorage.getItem("subpages"))
  );
  useEffect(() => {
    setpages(localStorage.getItem("page"));
    setsubpages(Number(localStorage.getItem("subpages")));
  }, [trigerer]);

  return (
    <React.Fragment>
      <AppBar color="primary" position="sticky" elevation={0}>
        <Toolbar>
          <Grid container spacing={1} alignItems="center">
            <Hidden smUp>
              <Grid item>
                <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  onClick={onDrawerToggle}
                  className={classes.menuButton}
                >
                  <MenuIcon />
                </IconButton>
              </Grid>
            </Hidden>
            <Grid item xs />
            <Grid item>
              <Link className={classes.link} href="#" variant="body2">
                Go to docs
              </Link>
            </Grid>
            <Grid item>
              <Tooltip title="Alerts â¢ No alerts">
                <IconButton color="inherit">
                  <NotificationsIcon />
                </IconButton>
              </Tooltip>
            </Grid>
            <Grid item>
              <IconButton color="inherit" className={classes.iconButtonAvatar}>
                <Avatar src="/static/images/avatar/1.jpg">
                  {localStorage.getItem("nama")?.split(" ")[0][0]}
                  {localStorage.getItem("nama")?.split(" ")[1][0]}
                </Avatar>
              </IconButton>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      <AppBar
        component="div"
        className={classes.secondaryBar}
        color="primary"
        position="static"
        elevation={0}
      >
        <Toolbar>
          <Grid container alignItems="center" spacing={1}>
            <Grid item xs>
              <Typography color="inherit" variant="h5" component="h1">
                {pages}
              </Typography>
            </Grid>
            <Grid item>
              <Button
                className={classes.button}
                variant="outlined"
                color="inherit"
                size="small"
                onClick={() => {
                  window.open(
                    "https://wa.me/6285155430353?text=Hi%20IT%20Support%20Akmali.%20Can%20you%20help%20me%20please?",
                    "_blank"
                  );
                }}
              >
                Contact for Help
              </Button>
            </Grid>
            <Grid item>
              <Tooltip title="Help">
                <IconButton color="inherit">
                  <HelpIcon />
                </IconButton>
              </Tooltip>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      {pages === "dashboard" ? (
        <AppBar
          component="div"
          className={classes.secondaryBar}
          color="primary"
          position="static"
          elevation={0}
        >
          <Tabs
            value={subpages}
            textColor="inherit"
            onChange={(e, newvalue) => {
              localStorage.setItem("subpages", String(newvalue));
              setsubpages(newvalue);
              refresh();
            }}
          >
            <Tab textColor="inherit" label="Monitoring" />
            <Tab textColor="inherit" label="Orders List" />
            {/*<Tab textColor="inherit" label="Templates" />
          <Tab textColor="inherit" label="Usage" /> */}
          </Tabs>
        </AppBar>
      ) : (
        <></>
      )}
    </React.Fragment>
  );
}

export default withStyles(styles)(Header);
