import React, { useState } from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import {
  createStyles,
  Theme,
  withStyles,
  WithStyles,
} from "@material-ui/core/styles";
import TitleIcon from "@material-ui/icons/Title";
import { MenuItem, Select, InputLabel, FormControl } from "@material-ui/core";
import axios from "axios";
import { Header, Url } from "../../../../api/config";

const styles = (theme: Theme) =>
  createStyles({
    paper: {
      maxWidth: 936,
      margin: "auto",
      overflow: "hidden",
    },
    searchBar: {
      borderBottom: "1px solid rgba(0, 0, 0, 0.12)",
    },
    searchInput: {
      fontSize: theme.typography.fontSize,
    },
    block: {
      display: "block",
    },
    addUser: {
      marginRight: theme.spacing(1),
    },
    OrdersWrapper: {
      margin: "20px 16px",
    },
    formControl: {
      width: "100%",
      marginTop: "1rem",
    },
  });

export interface OrdersProps extends WithStyles<typeof styles> {
  refresh: () => void;
}

function CategoryMenu(props: OrdersProps) {
  const { classes, refresh } = props;
  const [category, setcategory] = useState("");
  const submit = () => {
    axios
      .post(`${Url}/category/create`, { name: category }, { headers: Header })
      .then(function (response: any) {
        setcategory("");
        refresh();
      })
      .catch(function (error: any) {
        alert("title must be entered");
      });
  };
  return (
    <Paper className={classes.paper}>
      <AppBar
        className={classes.searchBar}
        position="static"
        color="default"
        elevation={0}
      >
        <Toolbar>
          <Grid container spacing={2} alignItems="center">
            <Grid item>
              <TitleIcon className={classes.block} color="inherit" />
            </Grid>
            <Grid item xs>
              <TextField
                fullWidth
                placeholder="Category Title"
                value={category}
                onChange={(e) => {
                  setcategory(e.target.value);
                }}
                InputProps={{
                  disableUnderline: true,
                  className: classes.searchInput,
                }}
              />
            </Grid>
            <Grid item>
              <Button
                variant="contained"
                color="primary"
                onClick={submit}
                className={classes.addUser}
                disabled={/([0-9]|[a-z])/.test(category) ? false : true}
              >
                Add Category
              </Button>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
    </Paper>
  );
}

export default withStyles(styles)(CategoryMenu);
